<template>
    <a-spin :spinning="loading">
      <a-form-model
        ref="form"
        :model="entity"
        :rules="rules"
        autocomplete="off"
        layout="vertical"
      >
        <div class="card">
          <a-row :gutter="24">
            <a-col :sm="6">
              <a-form-model-item label="计划名称" prop="planName" ref="planName">
                <a-input
                  v-model="entity.planName"
                  placeholder="请输入计划名称"
                  name="planName"
                  @change="inChange"
                ></a-input>
              </a-form-model-item>
            </a-col>
            <a-col :sm="12">
              <a-form-model-item label="描述" prop="description">
                <a-textarea
                  v-model:value="entity.description"
                  placeholder="描述"
                  :rows="4"
                  :max-length="1000"
                  name="description"
                />
              </a-form-model-item>
            </a-col>
          </a-row>
        </div>
        <g-collapse headerName="其他信息">
          <template #content>
            <a-row :gutter="24">
              <a-col :sm="6">
                <a-form-model-item label="创建人">
                  <span>admin</span>
                </a-form-model-item>
              </a-col>
              <a-col :sm="6">
                <a-form-model-item label="修改时间">
                  <span> 2025-04-20 15:00</span>
                </a-form-model-item>
              </a-col>
            </a-row>
          </template>
        </g-collapse>
      </a-form-model>
    </a-spin>
  </template>
  
  <script>
  import { GNdoPageEditBase } from "@/utils";
  export default {
    name: "CreateOrEditTrainingPlan",
    mixins: [GNdoPageEditBase],
    data() {
      return {
        entity: {
          planName: "",
          description: "", // 添加其他字段
        },
        rules: {
          planName: [
            { required: true, message: "计划名称不能为空", trigger: "change" },
            {
              pattern: /^[^\s]+(\s+[^\s]+)*$/,
              trigger: "change",
              message: "名称不能包含空格",
            },
          ],
          // 添加其他字段的校验规则
        },
      };
    },
  
    mounted() {},
  
    methods: {
      // 初始化
      init() {
        this.loading = true;
        if (this.id) {
          // 模拟请求
          setTimeout(() => {
            this.loading = false;
            this.entity = {
              planName: "测试计划",
              description: "计划描述",
              // 其他字段赋值
            };
          }, 500);
        } else {
          this.reset();
          this.loading = false;
        }
      },
      resetFormData() {
        this.entity = {
          planName: "",
          description: "", // 重置其他字段
        };
      },
      async save() {
        let res = await this.checkError();
        if (res) {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            this.saveSuccess(this.entity.planName);
            this.handleSaveEndBackView(this.id);
            this.refreshParent({
              reset: null,
            });
          });
        }
      },
  
      inChange(e) {
        console.log(e.target.value);
      },
    },
  };
  </script>
  
  <style lang="scss" scoped></style>